import React from 'react'
import computerImg from '../assets/imgs/compImg.png'
import meImg from '../assets/imgs/me.jpg'


export const AboutMe = () => {
    return (
        <div className="about-me">
            <section className='white p1'>
                <img className='computer-img' src={computerImg} alt="#" />
                <article>
                    <h2>Hi There, I'm</h2>
                    <h1>Shilo Ganon</h1>
                </article>
            </section>

            <section className='red p2'>
                <article className='txt'>

                    <aside>
                        <h3>דברים שאני צריך לשפר:</h3>
                        <ul>
                            <li>
                                להגיע לרמה גבוהה יותר מבחינת האנגלית
                            </li>
                            <li>
                                לשפר את יכולת כתיבת הקוד שלי ע"י הפידבק שאקבל ממך 😊
                            </li>
                            <li>
                                חייב ללמוד איך מכינית מצגת נורמאלית 🤦‍♂️
                            </li>
                        </ul>
                    </aside>

                    <aside>
                        <h3>קצת עלי בשביל היח"צ:</h3>
                        <ul>
                            <li>
                                אחראי
                            </li>
                            <li>
                                חרוץ
                            </li>
                            <li>
                                חברותי, תמיד שמח להכיר אנשים חדשים 😊
                            </li>
                            <li>
                                לא מפחד מעבודה קשה
                            </li>
                        </ul>

                    </aside>

                </article>
                <pre>
                    <img src={meImg} alt="#" />
                </pre>

            </section>

            <section className='white center p3'>
                <article>
                    <h3>על עצמי...</h3>
                    <aside>
                        <p>
                            התחביבים שלי הם כדורגל, פריזבי ושח.
                        </p>
                        <p>
                            לפני שלמדי פיתוח תוכנה הייתי בעלים של בית קפה ובנוסף למדתי לימודי כלכלה וניהול באוניברסיטה הפתוחה.
                        </p>
                        <p>
                            מאז שהתחלתי לכתוב קוד הבנתי שבזה ארצה לעסוק.
                        </p>
                        <p>
                            השאיפות שלי לטווח הקצר הם לשפר כמה שיותר את יכולת כתיבת הקוד שלי וללמוד דברים חדשים
                        </p>
                    </aside>

                </article>
                <hr />
                <article className='list center'>
                    <h3>התכנים שלמדתי בקורס ובעצמי:</h3>
                    <aside>
                        <ul>
                            <li>JavaScript</li>
                            <li>TypeScript(basic)</li>
                            <li>Css, Scss</li>
                            <li>HTML</li>
                            <li>NodeJS</li>
                        </ul>

                        <ul>
                            <li>React</li>
                            <li>Angular</li>
                            <li>Vue</li>
                            <li>Express</li>
                        </ul>

                        <ul>
                            <li>MongoDB</li>
                            <li>SQL(basic)</li>
                        </ul>
                    </aside>
                </article>
            </section>
        </div>
    )
}
