import { useState } from "react";
import { AboutMe } from "./cmps/AboutMe";
import { AboutMoveo } from "./cmps/AboutMoveo";

export const App = () => {
  const [aboutMe, setAboutMe] = useState(true)
  const [isChangeColor, setIsChangeColor] = useState(false)
  return (
    <div className="app">
      <section className={isChangeColor ? 'change-color' : ''}>

        <nav>
          <aside>
            <button onClick={() => setAboutMe(true)}>About me</button>
            <button onClick={() => setAboutMe(false)}>About moveo</button>
          </aside>

          <aside>
            <button onClick={() => setIsChangeColor(!isChangeColor)}>Change Color</button>
          </aside>
        </nav>

        <main>
          {
            aboutMe ? (
              <AboutMe />
            ) : (
              <AboutMoveo />
            )
          }
        </main>
      </section>
    </div>
  );
}

