import React from 'react'
import moveo from '../assets/imgs/moveo.jpg'
import img1 from '../assets/imgs/moveoImgs/1.png'
import img2 from '../assets/imgs/moveoImgs/2.png'
import img3 from '../assets/imgs/moveoImgs/3.png'

export const AboutMoveo = () => {
    return (
        <div className="about-moveo">
            <section className='moveo-p1'>
                <article>
                    <img src={moveo} alt="#" />
                    <pre>
                        <p>A little about moveo<span className="dot">.</span><span className="dot">.</span><span className="dot">.</span></p>
                    </pre>
                </article>
            </section>

            <section className='p2'>

                <article>
                    <h3>היסטוריה:</h3>
                    <p>חברת "מובאו" הוקמה לפני כ-6 שנים, החברה מונה 120 עובדים ושישה שותפים</p>
                    <p>החברה הוקמה בתור חברת סטארט-אפ, בנקודה מסויימת שותפיי החברה החליטו להתפצל, חלק</p>
                    <p>המשיכו לעסוק בענייני המוצר וחלק החלו לפתח למכור שירותי פיתוח תוכנה כדי לממן את החברה באותם זמנים.</p>
                    <p>ככל שעבר הזמן הביקוש לשירותי הפיתוח גבר וכך החליטה החברה להמשיך עם אותו הראיון ולהשאר חברת פיתוח</p>
                </article>

                <article className='imgs'>
                    <img src={img3} alt="#" />
                    <aside>
                        <h1>Clients</h1>
                        <p>We work with some amazing companies</p>
                    </aside>
                    <img src={img2} alt="#" />
                </article>
            </section>

            <section className='p3'>
                <h1>What We Can Do ?</h1>
                <img src={img1} alt="#" />

                <article>
                    <a onClick={() => window.open('https://www.moveo.group/')}>Contact us</a>
                </article>
            </section>

        </div>
    )
}
